import React, { useEffect } from "react"
import { Section, Card, Heading, Button, Alert, GifPlayer } from "components"
import { Row, Col } from "antd"
import { Link } from "react-router-dom"
import { Trans } from "@lingui/macro"
import { country } from "App"

export default function Resent({ location = {}, history }) {
	useEffect(() => {
		const state = location.state || {}
		if (!state.success) history.push(`/${country}/login`)
	}, [history, location.state])

	return (
		<Row type="flex" justify="center" align="middle">
			<Col lg={8}>
				<Section centered>
					<Link to={`/${country}`}>
						<img alt="" src="https://app.cudy.co/img/logo_live.png" width="100" />
					</Link>
				</Section>
				<Card autoHeight noHover>
					<Section textAlign="center" centered>
						<GifPlayer src="https://assets9.lottiefiles.com/temp/lf20_4Jjg2l.json" width={250} height={250} class="mb2em" />
						<Heading
							content={<Trans>It's been sent!</Trans>}
							subheader={
								<Trans>
									The verification email has been sent. Please check your inbox and click on it to get your account
									activated.
								</Trans>
							}
							marginBottom="3em"
						/>
						<Alert
							showIcon
							message={<Trans>Don't forget to check your spam/junk folder too :)</Trans>}
							type="info"
							style={{ marginBottom: "3em" }}
						/>
						<Link to={`/${country}/login`}>
							<Button type="primary" size="medium" icon="user">
								&nbsp; <Trans>Go to login</Trans>
							</Button>
						</Link>
					</Section>
				</Card>
			</Col>
		</Row>
	)
}
